import React, { useState } from 'react'; //Imprtant Module
import './ExpenseForm.css';


const ExpenseForm = (props) => {

    const [enteredTitle, setEnteredTitle] = useState('');
    const [enteredAmount, setEnteredAmount] = useState('');
    const [enteredDate, setEnteredDate] = useState('');

    //const [userInput, setUserInput] = useState({
    //enteredTitle: '',
    //enteredAmount: '',
    //enteredDate: '',
    // }); //*****  this is way to initialise multiple useState in one   *****

    const titleChangeHandler = (event) => {
        setEnteredTitle(event.target.value);

        //  setUserInput({
        //     ...userInput,
        //    enteredTitle: event.target.value,
        //});//*****   this is way to set Value in multiple useState  ****

    };

    const amountChangeHandler = (event) => {
        setEnteredAmount(event.target.value); //any value get by onChange,will be String, num also
    };

    const dateChangeHandler = (event) => {
        setEnteredDate(event.target.value);
    };

    const submitHandler = (event) => {
        event.preventDefault();// JS default method, it help to stay on page or not get reload, 

        const enxpenseData = {
            title: enteredTitle,
            amount: +enteredAmount,
            date: new Date(enteredDate),
        };

        props.onSaveExpenseData(enxpenseData); // passing Form data as object from ExpenseForm to Parent Component

        setEnteredTitle(''); // after submitting clear the form
        setEnteredAmount('');  // after submitting clear the form
        setEnteredDate('');  // after submitting clear the form
    };

    return (
        <form onSubmit={submitHandler}>
            <div className='new-expense__controls'>
                <div className='new-expense__control'>
                    <label>Title</label>
                    <input type="text"
                        value={enteredTitle}
                        onChange={titleChangeHandler} />
                </div>
                <div className='new-expense__control'>
                    <label>Amount</label>
                    <input
                        type="number"
                        min="0.01" step="0.01"
                        value={enteredAmount}
                        onChange={amountChangeHandler} />
                </div>
                <div className="new-expense__control">
                    <label>Date</label>
                    <input
                        type="date"
                        min="2019-0-01"
                        max="2022-12-31"
                        value={enteredDate}
                        onChange={dateChangeHandler} />
                </div>
            </div>
            <div className="new-expense__actions">
                <button type="submit">Add Expense</button>
            </div>
        </form>
    );

};

export default ExpenseForm;